.menu-toggle {
  min-height: 24px;
  min-width: 24px;

  @include flex();

  padding: 0;

  background-color: transparent;
  border: none;
  border-radius: 50%;
  outline: none;

  cursor: pointer;

  @media screen and (min-width: 768px) {
    display: none;
  }
}

.menu-toggle__icon {
  fill: $white-color;
}

.menu-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  padding-top: 80px;

  background-image: linear-gradient(336.93deg, #2d2a22 5.7%, #7e6d53 108.98%);
  z-index: 999;

  transform: translateX(100%);

  @include transition(transform);

  &.is-open {
    transform: translateX(0);
  }

  @media screen and(min-width: 768px) {
    display: none;
  }
}

.menu-container .menu-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
}

.mobile-menu__list {
  @include flex();

  flex-direction: column;
}

.mobile-menu__item {
  &:not(:last-child) {
    margin-bottom: 24px;
  }
}

.mobile-menu__link {
  display: block;

  font-family: 'Inter', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: calc(19 / 16);
  color: $light-color;
}
